import React from 'react'
import Tabs from '../Tabs'

const style = {
	root: {
		display: 'flex',
		flexDirection: 'column',
    	alignItems: 'center',
	},
	topDetails: {
		display: 'flex',
		width: '100%',
		position: 'relative'
	},
	direction: {
		alignItems: 'center',
		display: 'flex',
	},
	time: {
		fontWeight: 600,
		margin: '5px 15px'
	},
	label: {
		padding: '6px 10px',
	    backgroundColor: 'black',
	    color: 'white',
	    borderTopRightRadius: '5px',
	    borderBottomRightRadius: '5px',
	    fontWeight: '100',
	    height: 'fit-content',
	},
	detailText: {
		opacity: 0.6,
	}
}

class Details extends React.Component {
	
	state = {
		direction: false
	}

	flipDirection = () => this.setState(state=>({ direction: !state.direction}))

	render() {
		return(
			<div>
				<div style={style.root}>
					<div style={style.topDetails}>
						<div 
							onClick={this.flipDirection} 
							style={style.label}>
							{this.state.direction ? 'OUT' : 'RTN'}
						</div>

						<div>
							<div style={style.direction}>
								<div style={style.time}>10:26</div>
								<div>Bolton</div>
							</div>

							<section style={style.direction}>
								<div style={style.time}>10:56</div>
								<div>Buckshaw</div>
							</section>
						</div>
					</div>
					<Tabs/>
				</div>
				<div>
					<h4 style={{color: '#37c8b0', textAlign: 'center'}}>Ticket active</h4>
					<div>
						<div style={{...style.detailText, opacity: 1}}>Itinerary details</div>
						<div style={style.detailText}>Ticket type</div>
						<div style={style.detailText}>Ticket Ref</div>
						<div style={style.detailText}>Class</div>
						<div style={style.detailText}>Passengers</div>
						<div style={style.detailText}>Total price</div>
						<div style={style.detailText}>Transaction ID</div>
						<div style={style.detailText}>Purchased</div>
						<div style={style.detailText}>Date of travel</div>
					</div>
				</div>
			</div>
		)
	}
}

export default Details


