import React from 'react'
import Card from '../Card'

const style = {
	root: {
		width: '100%',
		display: 'flex',
		justifyContent: 'space-between',
		marginBottom: 18
	},
	tab: active => ({
		cursor: 'pointer',
		textAlign: 'center',
    	flex: 1,
    	padding: 8,
    	boxShadow: active ? '#37c8b0 0 4px 0px 0px' : 'none',
    	opacity: active ? 1 : 0.7
	})
}

class Tabs extends React.Component {
	state = {
		activeTab: 'ticket'
	}

	onClickTab = e => { 
		console.log(e.target.id) 
		this.setState({activeTab: e.target.id})
	}

	render() {
		return [
			<div style={style.root} key='select'>
				<div id='barcode' onClick={this.onClickTab} style={style.tab(this.state.activeTab === 'barcode')}>Barcode</div>
				<div id='ticket' onClick={this.onClickTab} style={style.tab(this.state.activeTab === 'ticket')}>Ticket</div>
			</div>,
			this.state.activeTab === 'ticket' ? <Card key='ticket'/> : null
		]
	}
}

export default Tabs


